import { Accordion, AccordionDetails, AccordionSummary, Button, List, ListItem, ListItemIcon, ListItemSecondaryAction, ListItemText, Paper, TextField, Typography } from "@material-ui/core";
import { navigate } from "gatsby";
import * as React from "react";
import ajax, { useFetcher } from "shared/components/ajax";
import { useModalTheme } from "shared/components/style";
import Defer from "shared/components/ui/Defer";
import PinPrompt from "shared/components/ui/market/PinPrompt";
import Navbar from "shared/components/ui/Navbar";
import { rupiah } from "shared/components/util";
// @ts-ignore
import icon from 'shared/images/Icon BPJS.png';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import TwoRow from "../../../../shared/components/ui/TwoRow";

const Page = () => {
    const modalTheme = useModalTheme();
    const [cust, setCust] = React.useState(null);
    const [prompt, setPrompt] = React.useState(0);
    const data = useFetcher(cust ? '/biller/asuransi/bpjskes?custid=' + cust : null, null, (err) => {
        alert(err.message);
        setCust(null);
    });

    const check = (e) => {
        e.preventDefault();
        setCust(e.currentTarget.account.value);
    }
    const submit = (pin) => {
        setPrompt(3);
        ajax.post('/biller/asuransi/bpjskes', {
            custid: data.custid,
            pin: pin,
        }).then(x => {
            alert('Berhasil');
            navigate('/digital/history/');
        }).catch(x => {
            alert(x.response?.data.message || x.message);
            setPrompt(2);
        });
    }
    return (
        <div>
            <Navbar />
            <form style={{ padding: '1em' }} onSubmit={check}>
                <List>
                    <ListItem>
                        <ListItemIcon><img src={icon} height="40px" alt="" /></ListItemIcon>
                        <ListItemText primary="Bayar Tagihan BPJS Kesehatan" />
                    </ListItem>
                    <ListItem>
                        <TextField name="account" label="ID Pelanggan" variant="filled" fullWidth />
                    </ListItem>
                    <ListItem>
                        <Button type="submit" variant="contained" color="primary">
                            Cek
                        </Button>
                        <ListItemSecondaryAction>
                            <Button variant="outlined" onClick={() => navigate('/user/')}>
                                Kembali
                            </Button>
                        </ListItemSecondaryAction>
                    </ListItem>
                </List>
            </form>
            <Defer data={data} visible={cust}>
                {() => (
                    <Paper elevation={5}><List>
                        <TwoRow label="ID Pelanggan" value={data.custid} />
                        <TwoRow label="Cabang" value={data.details.cabang} />
                        <TwoRow label="Tagihan" value={rupiah(data.bill)} />
                        <TwoRow label="Biaya Admin" value={rupiah(data.admin)} />
                        <TwoRow label="Total Tagihan" value={rupiah(data.total)} strong />
                        <ListItem>
                            <Accordion style={{ width: '100%' }}>
                                <AccordionSummary expandIcon={<ExpandMoreIcon />}>
                                    <Typography variant="h6">
                                        Rincian Tagihan
                                    </Typography>
                                </AccordionSummary>
                                <AccordionDetails style={{ flexDirection: 'column' }}>
                                    <List>
                                        {data.details.keterangan.map((x, i) => (
                                            <div key={i}>
                                                <TwoRow label="Nama" value={x.nama} />
                                                <TwoRow label="No. VA" value={x.noVa} />
                                                <TwoRow label="Premi" value={rupiah(x.premi)} />
                                                <TwoRow label="Saldo" value={rupiah(x.saldo)} />
                                            </div>
                                        ))}
                                    </List>
                                </AccordionDetails>
                            </Accordion>
                        </ListItem>
                        <ListItem>
                            <Button fullWidth color="primary" variant="contained"
                                style={{ marginLeft: 'auto' }} onClick={() => setPrompt(2)}>Beli</Button>
                        </ListItem>
                    </List></Paper>
                )}
            </Defer>
            <PinPrompt open={prompt >= 2} submitting={prompt === 3} onClose={() => setPrompt(0)} onAction={submit} />
        </div >
    );
}

export default Page
